.grid-container {
  display: grid;
  grid-gap: 0.625rem;
  grid-template-columns: auto;
  grid-template-rows: repeat(auto, 1fr);
  grid-template-areas: "slides" "content";
}
@media (min-width: 71.875em) {
  .grid-container {
    grid-template-columns: 35% 65%;
    grid-template-areas: "slides content content content";
  }
}
.bg-img {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.product-details {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .product-details {
    width: 90%;
    padding: 4.375rem 0.625rem;
  }
}

.product-content {
  display: flex;
  flex-direction: column;
  padding: 0 1.25rem 6.25rem;
}
@media (min-width: 71.875em) {
  .product-content {
    width: 60%;
    margin: 0 0 0 6.25rem;
    padding-top: 3.125rem;
  }
}
.product-content .product-content__company {
  text-transform: uppercase;
  font-size: 0.8125rem;
  color: hsl(26deg, 100%, 55%);
  font-weight: 600;
  letter-spacing: 0.09375rem;
  padding: 0.625rem 0;
}
.product-content .product-content__title {
  width: 100%;
}
.product-content .product-content__title h2 {
  font-size: 2.125rem;
  color: hsl(220deg, 13%, 13%);
}
@media (min-width: 71.875em) {
  .product-content .product-content__title h2 {
    font-size: 2.375rem;
  }
}
.product-content .product-content__desc {
  color: hsl(219deg, 9%, 45%);
  line-height: 1.75rem;
  font-size: 1.0625rem;
}
.product-content .product-content__price {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: hsl(220deg, 13%, 13%);
}
@media (min-width: 71.875em) {
  .product-content .product-content__price {
    flex-direction: column;
  }
}
.product-content .product-content__price .product-content__price-sec-one {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
}
.product-content .product-content__price .product-content__price-sec-one-main {
  font-size: 1.875rem;
  font-weight: bold;
  padding-right: 1.25rem;
}
.product-content .product-content__price .product-content__price-sec-one-discount span {
  background: hsl(25deg, 100%, 94%);
  color: hsl(26deg, 100%, 55%);
  border-radius: 0.3125rem;
  padding: 0.1875rem 0.5rem;
  font-weight: bold;
}
.product-content .product-content__price .product-content__price-old {
  font-weight: bold;
  color: hsl(220deg, 14%, 75%);
  text-decoration: line-through;
  font-size: 1.125rem;
}
@media (min-width: 71.875em) {
  .product-content .product-content__price .product-content__price-old {
    width: 100%;
  }
}
.product-content .product-content__cart {
  display: flex;
  flex-direction: column;
  margin: 0.625rem 0;
  justify-content: center;
  align-items: center;
}
@media (min-width: 71.875em) {
  .product-content .product-content__cart {
    flex-direction: row;
    padding-top: 1.25rem;
  }
}
.product-content .product-content__cart .product-content__cart-qty {
  height: 3.125rem;
  text-align: center;
  background: hsl(223deg, 64%, 98%);
  width: 100%;
  margin: 0.625rem 0 1.25rem 0;
}
@media (min-width: 71.875em) {
  .product-content .product-content__cart .product-content__cart-qty {
    width: 12.5rem;
  }
}
.product-content .product-content__cart .product-content__cart-qty .product-content__cart-qty-group {
  position: relative;
  width: 100%;
  height: 100%;
  background: hsl(223deg, 64%, 98%);
  border-radius: 0.625rem;
  overflow: hidden;
}
.product-content .product-content__cart .product-content__cart-qty #product_qty {
  height: 100%;
  width: 100%;
  text-align: center;
  background: transparent;
  border: none;
  font-size: 1.125rem;
  color: hsl(220deg, 13%, 13%);
}
.product-content .product-content__cart .product-content__cart-qty .product-content__cart-qty-decrement,
.product-content .product-content__cart .product-content__cart-qty .product-content__cart-qty-increment {
  position: absolute;
  cursor: pointer;
  display: inline-flex;
  height: 100%;
  width: 3.125rem;
  justify-content: center;
  align-items: center;
  color: hsl(26deg, 100%, 55%);
  font-weight: bold;
  font-size: 1.5rem;
  user-select: none;
}
.product-content .product-content__cart .product-content__cart-qty .product-content__cart-qty-decrement {
  left: 0;
}
.product-content .product-content__cart .product-content__cart-qty .product-content__cart-qty-increment {
  right: 0;
}
.product-content .product-content__cart .product-content__cart-add-to-cart {
  width: 100%;
}
@media (min-width: 71.875em) {
  .product-content .product-content__cart .product-content__cart-add-to-cart {
    width: 18.75rem;
    margin-left: 1.25rem;
  }
}
.product-content .product-content__cart .product-content__cart-add-to-cart span {
  background: url("../images/icon-cart-white.svg");
  width: 1.25rem;
  height: 1.25rem;
  display: block;
  margin-right: 1.25rem;
  background-repeat: no-repeat;
  background-position: center;
}